.wc-nav {
  &,
  .wc-nav-container {
    display: flex;
  }

  overflow-x: auto;
  margin: 0 auto;
  flex-direction: column;

  .wc-nav-container {
    flex-direction: row;
    justify-content: center;
    align-self: center;
    border-bottom: 1px solid #d2d2d7;
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .wc-nav-line-container {
    position: relative;

    .wc-nav-line {
      position: absolute;
      border-bottom: 1px solid #000;
      bottom: 0;
      left: 0;
      height: 0;
      width: 0;
      transition: all 0.3s ease-out;
    }
  }
  
}
